import React from 'react'
import {sendmessage} from "../../data/DIalogsReduser";
import Dialogs from "./Dialogs";
import {connect} from "react-redux";
import {AuthRedirect} from "../hocs/AuthRedirect";
import {compose} from "redux";


class DialogsContainer extends React.Component {


    render() {
        return (
            <Dialogs {...this.props} />
        )
    }
}

let StateToProps = (state) => ({MessagesData: state.MessagesData})

export default compose(connect(StateToProps, {sendmessage}),
    AuthRedirect)(DialogsContainer)